.header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 1rem;
    background: white;
    box-shadow: 0 .04rem .08rem 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
}
.header>div{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header .left,.header .right{
    width: .6rem;
}
.header .title{
    flex: 1 1;
    font-size: .32rem;
}
.textAreaContent {
  width: 100%;
  height: 4rem;
  padding: .2rem .2rem .4rem;
  position: relative; }
  .textAreaContent .inputArea {
    width: 100%;
    height: 100%;
    border: none; }
  .textAreaContent span {
    width: auto;
    height: .4rem;
    position: absolute;
    bottom: 0;
    left: .2rem;
    right: .2rem;
    text-align: right;
    text-indent: 2em;
    line-height: .4rem;
    padding: 0 .2rem;
    width: auto;
    color: gray;
    background: white;
    font-size: .28rem; }

.buttons {
  width: 100%;
  height: auto;
  padding: .1rem .2rem;
  display: flex;
  justify-content: space-between; }
  .buttons div {
    width: 2rem; }

